import React, { useState, forwardRef, useEffect } from "react";

import AutoSuggest, { RenderSuggestionsContainerParams } from "react-autosuggest";
import Tooltip from '../Tooltip'
import ValidationIndicator from '../ValidationIndicator'
import styled from 'styled-components'
var match = require('autosuggest-highlight/match')
var parse = require('autosuggest-highlight/parse');

import { getSuggestions, suggestionRule } from './SuggestSource'
import color from '../color-css/color'
import img from '../../assets/images/powered_by_google_on_white.png'
import { DropdownFooter, DropdownFooterContent, DropdownFooterImg, DropdownFooterContentHover } from './Footer'
import Label from '../Label'
import LabelIcon from '../Label/LabelIcon'

export type Props = {
  handleCantFindSuburbProp?: (selectedCantFindSuburb: boolean) => void,
  onSelectedItemProp?: (wasSelected: boolean) => void,
  inputFromProps?: string,
  isValidatedFromProp?: boolean,
  handleSuburbData?: (selectValue: string) => void
}

interface AutosuggestComponent {
  widthSize?: string,
  onHoverInputSuccess?: boolean,
  onSelectCantFindSuburb?: boolean,
}

interface Wrapper {
  widthSize?: string,
  onHoverInputSuccess?: boolean,
  onSelectCantFindSuburb?: boolean,
  pointerEvent?: string
}
const Input = styled.input`
  -webkit-appearance: none;
`
const StyledAutosuggestWrapper = styled.div<Wrapper>`
  border: 1px solid ${color.white};
  outline: none;
  margin-top: 1.25rem;
  font-family: museo-sans, sans-serif;

& .react-autosuggest__container {
  position: relative;
 }

& .react-autosuggest__input {
  font-family: museo-sans, sans-serif;
  margin-left: -2px;
  @media screen and (max-width: 1024px) {
    padding: 0.8125rem 1.625rem 0.75rem 0.9375rem;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }
  width: ${props => props.widthSize ? props.widthSize : "34rem"};
  height: 1.1875rem;
  background-color: ${props => props.onHoverInputSuccess === true ? color.blue : color.grey};
  transition: background 0.5s ease-in-out;
  border: 1px solid ${color.white};
  outline : none;
  color: ${props => props.onHoverInputSuccess === true ? color.blue_dark : color.grey_darker};
  font-size: 1rem;
  padding-left: 1.5625rem;
  border-radius: 0.625rem;
  padding: 0.75rem 5.3125rem 0.6875rem 1.5rem;
  cursor: pointer;	
  & ::placeholder,
  & ::-webkit-input-placeholder {
   color: ${color.grey_darker};
   font-family: museo-sans, sans-serif;
   font-size: 1rem;
   opacity: 1;
  }
  &:focus {
    ::-webkit-input-placeholder { color:transparent; }
    ::-moz-placeholder { color:transparent; }
    border: 1px solid ${color.blue_darker};
  }
 }

 & .react-autosuggest__input:hover{
  border: 1px solid ${color.blue_darker};
  transition: border 0.5s ease-in-out;
 }

& .react-autosuggest__suggestions-container--open {
  display: ${props => props.onSelectCantFindSuburb === true ? 'block' : 'none'};
  position: absolute;
  margin-top: 1.125rem;
  width: ${props => props.widthSize ? props.widthSize : "40.9375rem"};
  color: ${color.grey_29_percent};
  font-weight: 300;
  font-size: 1rem;
  letter-spacing: -0.05px;
  z-index: 1;
  @media screen and (max-width: 1024px) {
    width: 100%;
  }
  height: 225px;
  background: #fff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 3.125rem ${color.grey_shadow};
  border-radius: 0.625rem;
  & ::before {
    content: "";
    position: absolute;
    left: 20rem;
    @media screen and (max-width: 1024px) {
      left: 45vw;
    }
    top: -20px;
    width: 0;
    height: 0;
    border-top: 0.9375rem solid transparent;
    border-right: 0.9375rem solid #fff;
    border-bottom: 14px solid transparent;
    -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
  }
}

& .react-autosuggest__suggestions-list {
  margin: 0;
  width: 21.75rem;
  height: 8.3125rem;
  padding: 1.125rem 16.5625rem 0 1.75rem;
  list-style-type: none;
  font-weight: 300;
  font-size: 1rem;
  line-height: .625rem;
  overflow: auto;
  margin-top: 0.875rem;
  pointer-events: ${props => props.pointerEvent ? props.pointerEvent : ''};
  ::-webkit-scrollbar {
    width: 0.8125rem;
    height: 9.25rem;
    }
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px #EFEFEF; 
    border-radius: 0.625rem;
  }
  ::-webkit-scrollbar-thumb {
    background: #EFEFEF;
    border-radius: 0.625rem;
    z-index:1111;
  }
  @media screen and (max-width: 1024px) {
    width: auto;
    margin-right: 1.1875rem;
    padding-left: 1.1875rem;
    margin-top: 1.375rem;
  }
}

& .react-autosuggest__suggestion {
  cursor: pointer;	
  margin-bottom: 2.0625rem;
  @media screen and (max-width: 1024px) {
    min-width: max-content;
  }
}

& .react-autosuggest__suggestion:hover {
  color: ${color.blue_17_percent}
}

& .react-autosuggest__suggestion-match {
  font-weight: bold;
}
`

const HoveringWrapper = styled.div<AutosuggestComponent>`
cursor: 'pointer';
`

const StyledItalic = styled.i`
  font-size: 1rem;
  color: ${color.grey_darker};
  font-weight: 500;
  letter-spacing: -0.05px;
`

type Ref = HTMLInputElement

const SuburbAutosuggestField = forwardRef<Ref, Props>((props: any, aRef) => {
  const { inputFromProps, isValidatedFromProp, handleSuburbData, onSelectedItemProp } = props

  const [inputValue, setValue] = useState('')
  const [suggestions, setSuggestions] = useState<string[]>([])
  const [validSelection, setValidSelection] = useState(false)
  const [isMenuOpen, setIsMenuOpen] = useState(false)
  const [validationVisibility, setValidationVisibility] = useState(false)
  const [disableClickOnSuggestion, setDisableClickOnSuggestion] = useState('')
  const [labelIconState, setLabelIconStateState] = useState(true)
  const noResultString = 'No results found ...'
  const CANT_FIND_SUBURB = `Can't find suburb`

  const handleOnCantFindSubburb = (event: any) => {
    event.preventDefault()
    setValue(CANT_FIND_SUBURB)
    setIsMenuOpen(false)
    setValidSelection(true)
    setValidationVisibility(true)
    handleSuburbData ? handleSuburbData(CANT_FIND_SUBURB) : {}
  }

  const renderSuggestionsContainer = (params: RenderSuggestionsContainerParams) => {
    return (
      <div {...params.containerProps}>
        {params.children}
        {
          isMenuOpen &&
          <DropdownFooter>
            <DropdownFooterContentHover>
              <DropdownFooterContent
                onClick={() => {
                  handleOnCantFindSubburb(event)
                }}>
                Can't find your suburb?
              </DropdownFooterContent>
            </DropdownFooterContentHover>
            <DropdownFooterImg>
              <img src={img} style={{
                marginTop: `5px`
              }} />
            </DropdownFooterImg>
          </DropdownFooter>
        }
      </div>
    );
  };

  const renderFoundResults = (suggestion: string): React.ReactNode => {

    const matches = match(suggestion, inputValue);
    const parts = parse(suggestion, matches);

    return (
      <span>
        {parts.map((part: { text: string, highlight: boolean }, index: number) => {
          const className = part?.highlight ? 'react-autosuggest__suggestion-match' : '';
          return (
            <span className={className} key={index}>
              {part.text}
            </span>
          );
        })}
      </span>
    );
  }

  useEffect(() => {
    inputFromProps ? setValue(inputFromProps) : {}
    isValidatedFromProp ? setValidSelection(isValidatedFromProp) : {}
    isValidatedFromProp ? setValidationVisibility(isValidatedFromProp) : {}
    onSelectedItemProp ? onSelectedItemProp(isValidatedFromProp) : {}
  }, [inputFromProps, isValidatedFromProp])

  const inputProps = {
    placeholder: "Type your workplace suburb here",
    name: "workSuburb",
    value: inputValue,
    // @ts-ignore
    onChange: (event, { newValue, method }) => {
      setValue(newValue)
      setValidSelection(newValue === undefined || newValue === null)
    },
    onBlur: (_: any) => { },
    onFocus: (event: any) => { },
  };

  const storeInputReference = (autosuggest: any) => {
    if (autosuggest !== null) {
      aRef = autosuggest.input;
    }
  }

  return (
    <div>
      <Label>What suburb do you work in?
        <LabelIcon iconStates={labelIconState}>{'  '}*</LabelIcon>
      </Label>
      <HoveringWrapper>
        <StyledAutosuggestWrapper
          onHoverInputSuccess={validSelection}
          onSelectCantFindSuburb={isMenuOpen}
          pointerEvent={disableClickOnSuggestion}
        >
          <AutoSuggest
            suggestions={suggestions}
            focusInputOnSuggestionClick={false}
            onSuggestionsClearRequested={() => {
              setSuggestions([])
              setIsMenuOpen(false)
            }}
            onSuggestionsFetchRequested={({ value }) => {
              const valueGetter = getSuggestions(value)
              valueGetter.length > 0 ? setSuggestions(valueGetter) : setSuggestions([noResultString])
              setValue(value);
              setIsMenuOpen(true)
            }}
            onSuggestionSelected={(_, { suggestionValue }) => {
              const isValid = suggestionValue !== undefined
                && suggestionValue.length > 0
                && suggestionValue !== noResultString
              setValidSelection(isValid)
              setLabelIconStateState(isValid)
              let result = suggestionValue.substring(0, suggestionValue.indexOf(','))
              handleSuburbData ? handleSuburbData(result) : {}
              setValue(result);
            }}
            shouldRenderSuggestions={(value) => {
              return suggestionRule(value)
            }}
            getSuggestionValue={suggestion => suggestion}
            renderSuggestion={suggestion => {
              if (suggestion !== noResultString) {
                setDisableClickOnSuggestion('')
                return renderFoundResults(suggestion)
              }
              else {
                setDisableClickOnSuggestion('none')
                return (
                  <span>
                    <StyledItalic>{noResultString}</StyledItalic></span>
                )
              }
            }}
            inputProps={inputProps}
            renderSuggestionsContainer={renderSuggestionsContainer}
            //@ts-ignore
            renderInputComponent={(inputProps) => <Input onKeyPress={event => {
              if (event.key === 'Enter') {
                setIsMenuOpen(false)
              }
            }} {...inputProps}
              ref={storeInputReference}
            />}
          />
        </StyledAutosuggestWrapper>
      </HoveringWrapper>

      <Tooltip visibility={validationVisibility} inputName="suburbWork"
        widthSize="232px"
        tooltipTitle="Why do we need this?"
        tooltip_message="We need to know your workplace suburb to ensure 
        you’re signing up to the correct SDA branch."
      />

      <ValidationIndicator
        isCorrect={validSelection}
        {...{ message: "", visibility: validationVisibility }}
      />

    </div>
  )
})

export default SuburbAutosuggestField
