const renderDateString = (d:any)=>{
    var mm = (d.getMonth() + 1).toString().padStart(2, "0");
    var dd = d.getDate().toString().padStart(2, "0");
    var yy = d.getFullYear();
    var myDateString = dd + ' / ' + mm + ' / ' + yy;
    return myDateString;
}
export const getStartDate = (dayInWeek: string) => {
    let day = new Date()
    let formatDate = day.toLocaleTimeString('en-us', { weekday: 'long'})
    let currentDay = formatDate.substring(0 , formatDate.indexOf(' '))
    if (currentDay === dayInWeek) {
      return day.toLocaleDateString()
    }
    let dateInNumber: number
    let ret = new Date();
    switch(dayInWeek) {
      case "Monday": 
        dateInNumber = 1
        ret.setDate(ret.getDate() + (dateInNumber - 1 - ret.getDay() + 7) % 7 + 1);
        return renderDateString(ret)
      case "Tuesday": 
        dateInNumber = 2
        ret.setDate(ret.getDate() + (dateInNumber - 1 - ret.getDay() + 7) % 7 + 1);
        return renderDateString(ret)
      case "Wednesday":
        dateInNumber = 3
        ret.setDate(ret.getDate() + (dateInNumber - 1 - ret.getDay() + 7) % 7 + 1);
        return renderDateString(ret)
      case "Thursday": 
        dateInNumber = 4
        ret.setDate(ret.getDate() + (dateInNumber - 1 - ret.getDay() + 7) % 7 + 1);
        return renderDateString(ret)
      case "Friday": 
        dateInNumber = 5
        ret.setDate(ret.getDate() + (dateInNumber - 1 - ret.getDay() + 7) % 7 + 1);
        return renderDateString(ret)
      case "Saturday": 
        dateInNumber = 6
        ret.setDate(ret.getDate() + (dateInNumber - 1 - ret.getDay() + 7) % 7 + 1);
        return renderDateString(ret)
      case "Sunday": 
        dateInNumber = 7
        ret.setDate(ret.getDate() + (dateInNumber - 1 - ret.getDay() + 7) % 7 + 1);
        return renderDateString(ret)
    }
}